				</view>
				<view class="popup-content">
					<text class="popup-icon">üèÜ</text>
					<text class="popup-text">Á¥ØËÆ°ÊçêÁåÆÈ¶ôÁÅ´ÂÄºËææÂà∞ 5000 ÁÇπÔºåÂç≥ÂèØËé∑Âæó‰∏ìÂ±ûÁîµÂ≠êËØÅ‰π¶ÔºÅ</text>
					<text class="popup-text small">ËØÅ‰π¶Â∞ÜÂú®"ÊàëÁöÑËØÅ‰π¶"È°µÈù¢Â±ïÁ§∫ÔºåËßÅËØÅÊÇ®ÁöÑËôîËØö‰∏éÂäüÂæ∑</text>
				</view>
