</template>

<script>
	// ÂºïÂÖ•ËøîÂõûÂ§¥ÈÉ®ÁªÑ‰ª∂
	import pageBack from '/components/title/title.vue';
	import {
		useUserInfoStore
	} from "@/store/userInfo.js";
	import {
		incensePayAPI
	} from "@/apis/user.js";

	export default {
		name: 'IncensePage',
		components: {
			pageBack
		},
		data() {
			return {
				// Â§¥ÈÉ®Ê†áÈ¢òÈÖçÁΩÆ
				titleInfo: {
					titleShow: true,
					title: 'ÁîµÂ≠ê‰∏äÈ¶ô',
					imageUrl: 'http://t73sifiwt.hn-bkt.clouddn.com/pageBack/3.webp',
					heightShow: false,
					backShow: true
				},
				// ÈÄâ‰∏≠ÁöÑÈ¶ôÁÅ´Á±ªÂûã
				selectedIncenseType: '',
				// ÊçêÁåÆÈáëÈ¢ù
				donationAmount: '',
				// È¶ôÁÅ´Á±ªÂûãÂàóË°®
				incenseTypeList: [{
						label: 'ÊôÆÈÄöÈ¶ô',
						value: 'normal'
					},
					{
						label: 'È´òÈ¶ô',
						value: 'high'
					},
					{
						label: 'Ê™ÄÈ¶ô',
						value: 'sandalwood'
					},
					{
						label: 'Ê≤âÈ¶ô',
						value: 'agarwood'
					}
				],
				// Âø´Êç∑ÈáëÈ¢ùÂàóË°®
				quickAmountList: [9, 19, 29, 39, 99, 199],
				// Êñ∞Â¢ûÔºöÊªöÂä®ÂÖ¨ÂëäÁõ∏ÂÖ≥
				noticeList: [], // ÂÖ¨ÂëäÂàóË°®
				scrollDuration: 30, // ÊªöÂä®Âä®ÁîªÊó∂Èïø
				noticeTimer: null, // ÂÆöÊó∂Âô®
				// Ê®°ÊãüÁî®Êà∑ÊòµÁß∞Ê±†
				nicknamePool: ['Ê∏ÖÈ£éÂ±ÖÂ£´', 'ÂñÑÁî∑‰ø°Â•≥', 'ÈöèÁºòÂÆ¢', 'ÈùôÂøÉ‰∫∫', 'Á¶èËøêÂêõ', 'Âπ≥ÂÆâÂÆ¢', 'ÂêâÁ••ÁøÅ', 'Â¶ÇÊÑèÂßë']
			};
		},
		onLoad() {
			// ÂàùÂßãÂåñÂÖ¨ÂëäÂàóË°®
			this.initNoticeList();
			// ÂêØÂä®ÂÆöÊó∂Êõ¥Êñ∞ÂÖ¨Âëä
			this.startNoticeTimer();
			// Ê£ÄÊü•ÊòØÂê¶È¶ñÊ¨°ËøõÂÖ•
			this.checkFirstVisit();
		},
		onUnload() {
			// Ê∏ÖÈô§ÂÆöÊó∂Âô®ÔºåÈò≤Ê≠¢ÂÜÖÂ≠òÊ≥ÑÊºè
			if (this.noticeTimer) {
				clearInterval(this.noticeTimer);
				this.noticeTimer = null;
			}
		},
		methods: {
			/**
			 * Ê£ÄÊü•ÊòØÂê¶È¶ñÊ¨°ËøõÂÖ•
			 */
			checkFirstVisit() {
				const hasVisited = uni.getStorageSync('incense_first_visit');
				if (!hasVisited) {
					// È¶ñÊ¨°ËøõÂÖ•ÔºåÊòæÁ§∫ÂºπÁ™ó
					setTimeout(() => {
						this.$refs.firstVisitPopup.open();
					}, 300);
				}
			},
			/**
			 * ÂÖ≥Èó≠È¶ñÊ¨°ËøõÂÖ•ÂºπÁ™ó
			 */
			closeFirstVisitPopup() {
				// Ê†áËÆ∞Â∑≤ËÆøÈóÆ
				uni.setStorageSync('incense_first_visit', true);
				this.$refs.firstVisitPopup.close();
			},
			/**
			 * Êñ∞Â¢ûÔºöÂàùÂßãÂåñÂÖ¨ÂëäÂàóË°®
			 */
			initNoticeList() {
				// ÂàùÂßãÂåñ5Êù°Ê®°ÊãüÊï∞ÊçÆ
				for (let i = 0; i < 5; i++) {
					this.addNewNotice();
				}
			},
			/**
			 * Êñ∞Â¢ûÔºöÊ∑ªÂä†Êñ∞ÁöÑÂÖ¨Âëä
			 */
			addNewNotice() {
				// ÈöèÊú∫ÈÄâÊã©ÊòµÁß∞
				const randomNickname = this.nicknamePool[Math.floor(Math.random() * this.nicknamePool.length)];
				// ÈöèÊú∫ÈÄâÊã©È¶ôÁÅ´Á±ªÂûã
				const randomIncense = this.incenseTypeList[Math.floor(Math.random() * this.incenseTypeList.length)].label;
				// ÈöèÊú∫ÈáëÈ¢ù
				const randomAmount = this.quickAmountList[Math.floor(Math.random() * this.quickAmountList.length)];
				// ÊûÑÂª∫ÂÖ¨ÂëäÊñáÊú¨
				const noticeText = `${randomNickname} ‰æõÂ•â‰∫Ü${randomIncense}ÔºåÊçêÁåÆÔø•${randomAmount} `;
				// Ê∑ªÂä†Âà∞ÂÖ¨ÂëäÂàóË°®
				this.noticeList.unshift(noticeText);
				// ÈôêÂà∂ÂàóË°®ÈïøÂ∫¶Ôºå‰øùÊåÅÊÄßËÉΩ
				if (this.noticeList.length > 10) {
					this.noticeList.pop();
				}
			},
			/**
			 * Êñ∞Â¢ûÔºöÂêØÂä®ÂÖ¨ÂëäÂÆöÊó∂Âô®
			 */
			startNoticeTimer() {
				// ÊØè5-10ÁßíÈöèÊú∫Ê∑ªÂä†‰∏ÄÊù°Êñ∞ÂÖ¨Âëä
				this.noticeTimer = setInterval(() => {
					this.addNewNotice();
					// ÈöèÊú∫Ë∞ÉÊï¥ÊªöÂä®ÈÄüÂ∫¶ÔºåÂ¢ûÂä†ÁúüÂÆûÊÑü
					this.scrollDuration = 25 + Math.random() * 10;
				}, 5000 + Math.random() * 5000);
			},
			/**
			 * ÊâìÂºÄÂºπÁ™ó
			 */
			openPopup() {
				this.$refs.incensePopup.open();
			},
			/**
			 * ÂÖ≥Èó≠ÂºπÁ™ó
			 */
			closePopup() {
				this.$refs.incensePopup.close();
				// ÈáçÁΩÆÊï∞ÊçÆ
				this.selectedIncenseType = '';
				this.donationAmount = '';
			},
			/**
			 * ÈÄâÊã©È¶ôÁÅ´Á±ªÂûã
			 */
			selectIncenseType(type) {
				this.selectedIncenseType = type;
			},
			/**
			 * ÈÄâÊã©Âø´Êç∑ÈáëÈ¢ù
			 */
			selectQuickAmount(amount) {
				this.donationAmount = amount.toString();
			},
			/**
			 * Á°ÆËÆ§‰æõÂ•â
			 */
			confirmOffer() {
				const store = useUserInfoStore();
				if (!store.$state.userInfo.id) {
					return uni.showModal({
						title: "ËØ∑ÂÖàÁôªÂΩï",
						content: "ÁôªÂΩïÂêéÊâçÂèØ‰ª•‰æõÂ•âÈ¶ôÁÅ´Âπ∂Á¥ØËÆ°È¶ôÁÅ´ÂÄº",
						showCancel: false,
						success: () => {
							uni.navigateTo({
								url: "/pages/login/login"
							});
						}
					});
				}

				// È™åËØÅÊòØÂê¶ÈÄâÊã©‰∫ÜÈ¶ôÁÅ´Á±ªÂûã
				if (!this.selectedIncenseType) {
					uni.showToast({
						title: 'ËØ∑ÈÄâÊã©È¶ôÁÅ´Á±ªÂûã',
						icon: 'none',
						duration: 2000
					});
					return;
				}

				// È™åËØÅÊòØÂê¶ËæìÂÖ•‰∫ÜÈáëÈ¢ù
				if (!this.donationAmount || parseFloat(this.donationAmount) <= 0) {
					uni.showToast({
						title: 'ËØ∑ËæìÂÖ•ÊçêÁåÆÈáëÈ¢ù',
						icon: 'none',
						duration: 2000
					});
					return;
				}

				// È™åËØÅÈáëÈ¢ùÊ†ºÂºè
				const amount = parseFloat(this.donationAmount);
				if (isNaN(amount) || amount <= 0) {
					uni.showToast({
						title: 'ËØ∑ËæìÂÖ•ÊúâÊïàÁöÑÈáëÈ¢ù',
						icon: 'none',
						duration: 2000
					});
					return;
				}

				// Ëé∑ÂèñÈÄâ‰∏≠ÁöÑÈ¶ôÁÅ´Á±ªÂûãÂêçÁß∞
				const selectedType = this.incenseTypeList.find(item => item.value === this.selectedIncenseType);

				// Ê®°ÊãüÂæÆ‰ø°ÊîØ‰ªò + Á¥ØÁßØÈ¶ôÁÅ´ÂÄº
				uni.showLoading({
					title: "ÂæÆ‰ø°ÊîØ‰ªò‰∏≠",
					mask: true
				});
				setTimeout(() => {
					incensePayAPI({
						user_id: store.$state.userInfo.id,
						amount
					}).then((res) => {
						uni.hideLoading();
						if (res.status === 200) {
							// Êõ¥Êñ∞Êú¨Âú∞È¶ôÁÅ´ÂÄº
							store.$state.userInfo.balance = res.newBalance;

							// Êñ∞Â¢ûÔºöÂ∞ÜÂΩìÂâçÁî®Êà∑ÁöÑÊçêÁåÆ‰ø°ÊÅØÊ∑ªÂä†Âà∞ÊªöÂä®ÂÖ¨Âëä
							const userNotice = `ÊÇ® ‰æõÂ•â‰∫Ü${selectedType ? selectedType.label : 'È¶ôÁÅ´'}ÔºåÊçêÁåÆÔø•${amount} ÔºåÈ¶ôÁÅ´ÂÄºÂ∑≤ÂÖ•Ë¥¶`;
							this.noticeList.unshift(userNotice);
							if (this.noticeList.length > 10) {
								this.noticeList.pop();
							}

							uni.showToast({
								title: `Â∑≤‰æõÂ•â${selectedType ? selectedType.label : 'È¶ôÁÅ´'}ÔºåÈ¶ôÁÅ´ÂÄº+${amount}`,
								icon: 'success',
								duration: 2500
							});
							// ÂÖ≥Èó≠ÂºπÁ™ó
							this.closePopup();
							// Ë∞ÉÁî®ÂéüÊúâÁöÑ‰∏äÈ¶ôÊñπÊ≥ï
							this.offerIncense();
						} else {
							uni.showToast({
								title: res.message || "ÊîØ‰ªòÂ§±Ë¥•ÔºåËØ∑ÈáçËØï",
								icon: "none"
							});
						}
					}).catch(() => {
						uni.hideLoading();
						uni.showToast({
							title: "ÁΩëÁªúÂºÇÂ∏∏ÔºåËØ∑Á®çÂêéÈáçËØï",
							icon: "none"
						});
					});
				}, 600);

			},
			/**
			 * ‰∏äÈ¶ôÊìç‰ΩúÊñπÊ≥ï
			 * ÊèêÁ§∫È¶ôÁÅ´Â∑≤Â•â‰∏ä
			 */
			offerIncense() {
				// Ëøô‰∏™ÊñπÊ≥ï‰øùÁïôÔºåÂ¶ÇÊûúÂêéÁª≠ÊúâÂä®ÁîªÊàñÂÖ∂‰ªñÈÄªËæëÂèØ‰ª•Âú®ËøôÈáåÊ∑ªÂä†
			}
		}
	};
</script>

<style lang="less" scoped>
	// È°µÈù¢Êï¥‰ΩìÊ†∑Âºè
