		overflow: hidden;
	}

	// Êñ∞Â¢ûÔºöÊªöÂä®ÂÖ¨ÂëäÊ†∑Âºè
	.scroll-notice {
		top: 200rpx;
		position: relative;
		width: 100%;
		height: 60rpx;
		line-height: 60rpx;
		overflow: hidden;
		z-index: 9;

		.notice-content {
			position: absolute;
			left: 100%;
			top: 0;
			white-space: nowrap;
